<template>
  <div id="newsInfo">
    <h3>{{info.title}}</h3>
    <h5>{{info.ctime|datetimeFilter}}</h5>
    <p>{{info.content}}</p>
    <comment-box :id="id"></comment-box>
  </div>
</template>
<script>
import comment from "../sub/comment.vue";
export default {
  data() {
    return { info: [],
     id: this.$route.query.id,
     topShow:true,//
     leftShow:true,//
     bottomShow:true,//
      };
  },
  created() {
    this.getNewsInfo();
    this.topIsShow();
    this.leftIsShow();
    this.bottomIsShow();
  },
  methods: {
    //header 
    topIsShow(){
      this.$emit('isShow',this.topShow)
    },
    //  
    leftIsShow(){
      this.$emit('leftIsShow',this.leftShow)
    },
    // tabbar 
    bottomIsShow(){
      this.$emit('bottomIsShow',this.bottomShow)
    },
    getNewsInfo() {
      var id = this.$route.query.id;
      // console.log(this.$route.query.id);
      var url = "http://skyrinbyliu.applinzi.com/getNewsInfo?id=" + id;
      this.axios.get(url).then(result => {
        this.info = result.data.data;
        console.log(this.info);
      });
    }
  },
  components: { "comment-box": comment }
};
</script>
<style scoped>
h3 {
  font-size: 18px;
  text-align: center;
}
h5 {
  text-align: right;
  padding-right: 20px;
}
p {
  font-size: 14px;
}
</style>
